export default {
	data() {
		return {
		laywerList:[],
		intellectualList:[],
		taxList:[],
		humanList:[],
		officeList:[],
		brandList:[],
		goodMoneyList:[],
		cleanList:[],
		logisticsList:[],
		}
	},
	onLoad:function(){ 
		var _self = this
		wx.showShareMenu()
		if(!uni.getStorageSync("openId")){
			uni.navigateTo({
				url:"../load/load"
			})
		}
		_self.getLawyer()
		_self.getintellectual()
		_self.getTax()
		_self.getHuman()
		_self.getOffice()
		_self.getBrand()
		_self.getGoodMoney()
		_self.getClean()
		_self.getLogistics()
	},
	onPullDownRefresh:function() {
		var _self = this
		if(!uni.getStorageSync("openId")){
			uni.navigateTo({
				url:"../load/load"
			})
		}
		_self.getLawyer()
		_self.getintellectual()
		_self.getTax()
		_self.getHuman()
		_self.getOffice()
		_self.getBrand()
		_self.getGoodMoney()
		_self.getClean()
		_self.getLogistics()
	},
	methods: {
		/** 
		 * 获取法律服务
		 */
		getLawyer:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/totalQuery",
				data:{id:"0",optionId:uni.getStorageSync("openId"),pull:3,branch:0},
				method:"GET",
				success:function(res){ 
					var data = res.data
					_self.laywerList = data.slice(0,2)
				}
				
			})
		},
		goToDetail:function(content,id){
			uni.navigateTo({
				url:"../serverPage/"+content+"/"+content+"?id="+id
			})
		},
		/**
		 * 更多
		 */
		learMore:function(content){
			uni.navigateTo({
				url:"../serverPage/"+content+"/"+content
			})
		},
		/**
		 * 知识产权
		 */
		getintellectual:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/totalQuery",
				data:{id:"0",optionId:uni.getStorageSync("openId"),pull:9,branch:0},
				method:"GET",
				success:function(res){ 
					var data = res.data
					_self.intellectualList = data.slice(0,2)
				}
				
			})
		},
		/**
		 * 税务服务
		 */
		getTax:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/totalQuery",
				data:{id:"0",optionId:uni.getStorageSync("openId"),pull:10,branch:0},
				method:"GET",
				success:function(res){ 
					var data = res.data
					_self.taxList = data.slice(0,2)
				}
				
			})
		},
		/**
		 * 人力资源
		 */
		getHuman:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/totalQuery",
				data:{id:"0",optionId:uni.getStorageSync("openId"),pull:11,branch:0},
				method:"GET",
				success:function(res){ 
					var data = res.data
					_self.humanList = data.slice(0,2)
				}
				
			})
		},
	/**
	 * 办公用品
	 */
	getOffice:function(){
		var _self = this
		uni.request({
			url:_self.$api+"dockingManager/totalQuery",
			data:{id:"0",optionId:uni.getStorageSync("openId"),pull:7,branch:0},
			method:"GET",
			success:function(res){ 
				var data = res.data
				_self.officeList = data.slice(0,2)
			}
			
		})
	},
	/**
	 * 品牌设计
	 */
	getBrand:function(){
		var _self = this
		uni.request({
			url:_self.$api+"dockingManager/totalQuery",
			data:{id:"0",optionId:uni.getStorageSync("openId"),pull:8,branch:0},
			method:"GET",
			success:function(res){ 
				var data = res.data
				_self.brandList = data.slice(0,2)
			}
			
		})
	},
	/**
	 * 高薪申报
	 */
	getGoodMoney:function(){
		var _self = this
		uni.request({
			url:_self.$api+"dockingManager/totalQuery",
			data:{id:"0",optionId:uni.getStorageSync("openId"),pull:12,branch:0},
			method:"GET",
			success:function(res){ 
				var data = res.data
				_self.goodMoneyList = data.slice(0,2)
			}
			
		})
	},
	/**
	 * 家政清洁
	 */
	getClean:function(){
		var _self = this
		uni.request({
			url:_self.$api+"dockingManager/totalQuery",
			data:{id:"0",optionId:uni.getStorageSync("openId"),pull:13,branch:0},
			method:"GET",
			success:function(res){ 
				var data = res.data
				_self.cleanList = data.slice(0,2)
			}
			
		})
	},
		/**
		 * 快递物流
		 */
		getLogistics:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/totalQuery",
				data:{id:"0",optionId:uni.getStorageSync("openId"),pull:14,branch:0},
				method:"GET",
				success:function(res){ 
					var data = res.data
					_self.logisticsList = data.slice(0,2)
					 uni.stopPullDownRefresh();
				}
				
			})
		},
		
	}
}
